<template>
	<div id="app">
		<router-view />
	</div>
</template>

<script>
export default {
	name: 'App',
	data() {
		return {
			socket: null
		}
	},
	mounted() {
		this.$store.state.version = this.getVersion()
		if (localStorage.getItem('token')) {
			this.start()
		}
	},
	methods: {
		start: function() {
			this.$EventBus.$on('transportSocketId', data => {
				//
				this.socket.emit('askOffline', data)
			})
			// console.log(navigator)

			this.socket = this.$io('http://iwen.link:9001', {
				transportOptions: {
					polling: {
						extraHeaders: {
							appname: navigator.appName,
							platform: navigator.platform,
							useragent: this.getVersion()
						}
					}
				}
			})

			this.socket.on('connect', () => {
				this.socket.on('info', data => {
					//
					let arr = []
					Object.keys(data).map(key => {
						arr.push(data[key])
					})
					this.$store.state.deviceLsit = arr

					// console.log(data)
				})

				this.socket.on('offline', () => {
					//
					localStorage.removeItem('token')
					this.$router.push('/login')
					this.socket.close()
				})
			})
		},

		getVersion: function() {
			let useragent = ''
			if (navigator.userAgent.toLowerCase().indexOf('electron') > -1)
				useragent = 'electron'
			else
				useragent = /ipad|iphone|midp|rv:1.2.3.4|ucweb|android|windows ce|windows mobile/.test(
					navigator.userAgent.toLowerCase()
				)
					? 'phone'
					: 'web'

			return useragent
		}
	}
}
</script>

